@use 'base/mixins' as m;

body{
    background-color: black;
    color: white;
}

.dashboard-header{
    padding-top: 1rem;
}

.dheader__logo{
    
    padding-top: .5rem;
}
.dheader__contenedor{
    margin: 0 auto;
  
    padding-right: 5rem;
    display: flex;
    justify-content: space-between;
}

.dheader__navegacion{
   
 
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 5rem;
  
    
}

.nombre{
   
}


.notificacion{
    display: block;
    position: absolute;
   & a{
       background-color: red;
       color: white;
       font-size: 1.5rem;
       border-radius: 50%;
       width: 3rem;
       height: 3rem;
       margin: 0;
       padding: 0;
       display: flex;
       justify-content: center;
       align-items: center;
       position: relative;
       right: 6.5rem;
       bottom: 3rem;
   }
}